<template>
  <div id="app">
    <div class="flex flex-col container mx-auto">
      <h1 class="text-3xl font-bold text-gray-700 mb-12 select-none">Список вопросов</h1>

      <div class="flex flex-row items-center justify-center w-full mx-auto mb-7">
        <div
          v-for="(item, index) in sections"
          :key="index"
          :class="[
            'flex flex-row items-center justify-center px-6 py-4 rounded-lg shadow-md mr-4 cursor-pointer bg-blue-200 hover:bg-blue-400 select-none text-gray-800 font-semibold',
            activeTab.index === index ? 'bg-blue-400' : ''
          ]"
          @click="selectTab(index)"
        >
          {{ item }}
        </div>
      </div>

      <div class="comment">
        <div
          v-for="(item, key) in questions"
          :key="key"
          class="comment__item"
        >
          <h1 class="comment__h1">{{ item.username }}</h1>
          <p class="comment__p">{{ item.comment }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getDatabase, ref, onValue} from 'firebase/database';

export default {
  name: 'App',
  data: () => {
    return {
      result: null,
      activeTab: {
        index: null,
        name: null
      }
    };
  },
  computed: {
    questions() {
      if (!this.result) return false;
      return this.result[this.activeTab.name];
    },
    sections() {
      if (!this.result) return false;
      return Object.keys(this.result);
    }
  },
  async mounted() {
    const db = getDatabase();
    const starCountRef = ref(db);

    onValue(starCountRef, (snapshot) => {
      this.result = snapshot.val();
      this.activeTab = {
        index: 0,
        name: Object.keys(this.result)[0]
      };
    });
  },
  methods: {
    selectTab(index) {
      this.activeTab = {
        name: this.sections[index],
        index
      };
    }
  }
}
</script>

